import { Test } from '@nestjs/testing'
import { TasksService } from './tasks.service'
import { TaskRepository } from './task.repository'
import { GetTasksFilterDto } from './dto/get-tasks-filter.dto'
import { TaskStatus } from './task-status.enum'
import { NotFoundException } from '@nestjs/common'
import { CreateTaskDto } from './dto/create-task.dto'
import mock = jest.mock

const mockUser = { username: 'Test user', id: 12 }

const mockTaskRepository = () => ({
	getTasks: jest.fn(),
	findOne: jest.fn(),
	createTask: jest.fn(),
	delete: jest.fn()
})

describe('TaskService', () => {
	let tasksService
	let taskRepository

	beforeEach(async () => {
		const module = await Test.createTestingModule({
			providers: [
				TasksService,
				{ provide: TaskRepository, useFactory: mockTaskRepository }
			],
		}).compile()

		tasksService = await  module.get<TasksService>(TasksService)
		taskRepository = await module.get<TaskRepository>(TaskRepository)
	})

	describe('getTasks', () => {
		it('Gets all tasks from the repository', async () => {
			taskRepository.getTasks.mockResolvedValue('someValue')

			expect(taskRepository.getTasks).not.toHaveBeenCalled()
			const filters: GetTasksFilterDto = { status: TaskStatus.IN_PROGRESS, search: 'Some search query'}
			// Call tasksService.getTasks
			const result = await tasksService.getTasks(filters, mockUser)
			expect(taskRepository.getTasks).toHaveBeenCalled()
			expect(result).toEqual('someValue')
			// expect taskRepository.getTasks TO HAVE BEEN CALLED
		})
	})

	describe('getTaskById',() => {
		it('calls taskRepository.findOne() and successfully retrieve and return the task', async () => {
			const mockTask = { title: 'Test task', description: 'Test desc' }
			taskRepository.findOne.mockResolvedValue(mockTask)

			const result = await tasksService.getTaskById(1, mockUser)
			expect(result).toEqual(mockTask)

			expect(taskRepository.findOne).toHaveBeenCalledWith({ where: { id: 1, userId: mockUser.id }})
		})

		it('throw an error as task is not found', () => {
			taskRepository.findOne.mockResolvedValue(null)
			expect(tasksService.getTaskById(1, mockUser)).rejects.toThrow(NotFoundException)
		})
	})

	describe('createTask', () => {
		it('calls taskRepository.create() and return the result', async () => {
			taskRepository.createTask.mockResolvedValue('success')

			expect(taskRepository.createTask).not.toHaveBeenCalled()
			const createTaskDto: CreateTaskDto = { title: 'Hello world', description: 'We need save this world form you'}
			const result = await tasksService.createTask(createTaskDto, mockUser)
			expect(taskRepository.createTask).toHaveBeenCalledWith(createTaskDto, mockUser)
			expect(result).toEqual('success')
		})
	})

	describe('deleteTask', () => {
		it('calls taskRepository.deleteTask() to delete a task', async () => {
			taskRepository.delete.mockResolvedValue({ affected: 1})

			expect(taskRepository.delete).not.toHaveBeenCalled()
			await tasksService.deleteTask(1, mockUser)
			expect(taskRepository.delete).toHaveBeenCalledWith(1, mockUser)
		})

		it('throws an error as task could not be found', () => {

		})
	})
})